<template>
	<view class="container">
		<watermark></watermark>
		<!-- 上面部分 -->
		<view class="fixedTop">
			<u-tabs :list="list" :is-scroll="false" :current="current" @change="change" height="110" bg-color="#03958C" inactive-color="#fff" active-color="#fff" :active-item-style="activeStyle"></u-tabs>
		</view>
		<template>
			<view class="padiingTop90" v-if="current==0">
				<tabs1 :chartData="chartData" :timeSelect="timeSelect" :timeUnit="form.timeUnit" :tagData="tagData" @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></tabs1>
			</view>
			<view class="padiingTop90" v-if="current==2">
				<tabs2 :chartData="chartData" :timeSelect="timeSelect" :timeUnit="form.timeUnit" :tdList="tdList" @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap" @twoChange="_twoChange"></tabs2>
			</view>
			<view class="padiingTop90" v-if="current==1">
				<tabs3 :chartData="chartData" :timeSelect="timeSelect" :timeUnit="form.timeUnit" :tdList="tdList" @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap" @twoChange="_twoChange"></tabs3>
			</view>
		</template>
		<tabBar></tabBar>
	</view>
</template>

<script>
	import tabBar from '@/components/tabBar2.vue';
	import tabs1 from '@/pages/salemanagerVisual/case/components/tabs1.vue';
	import tabs2 from '@/pages/salemanagerVisual/case/components/tabs2.vue';
	import tabs3 from '@/pages/salemanagerVisual/case/components/tabs3.vue';
	import watermark from '@/components/watermark.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import { bus } from '@/utils/bus.js';
	export default {
		data() {
			return {
				activeStyle: {
					fontSize:'36rpx'
				},
				isshow:false,
				current: 0,
				list: [{
					name: '数据统计'
				}, {
					name: '转化情况'
				}, {
					name: '业绩排名'
				}],
				form: {
					tabCode: '', // tabs 0数据统计 1业绩排名 2转化情况
					tabCodeSub: '', // 子tabs值
					timeUnit: '2', // 时间统计 0年 1季 2月 3周
					time: '', // 当前统计格式 prev/next值
				},
				timeSelect: { // 柱状图选择
					prev: '',
					next: '',
				},
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
				// 柱状图
				chartData: {
					categories: [],
					series: [{
						data: []
					}],
				},
				tdList:[], // 表格数据
				tagData:{} // 数据统计数据
			}
		},
		onLoad(){
			this.getList()
		},
		methods: {
			// 获取列表
			getList() {
				let that = this
				that.form.tabCode = that.current == 0 ? 0 : (that.current == 1 ? 2 : 1)
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('4001', Object.assign({}, that.form, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.timeSelect.prev = res.data.data.histogramData.pre
						that.timeSelect.next = res.data.data.histogramData.next
						that.chartData.series[0].data = res.data.data.histogramData.yData
						that.$set(that.chartData, 'series', that.chartData.series)
						that.$set(that.chartData, 'categories', res.data.data.histogramData.xData)
						bus.$emit('changeEcharts',that.chartData)
						if(that.form.tabCode != 0){
							that.tdList = that.etype == 3 ? that.tdList.concat(res.data.data.tableData.list) : res.data.data.tableData.list
							that.totalPages = res.data.data.tableData.totalPage
						}else{
							that.tagData = res.data.data.tagData
						}
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
					}
				})
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this.getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this.flag = true
					this.getList()
				}
			},
			// 柱状图左翻
			_prevTap() {
				this.form.time = this.timeSelect.prev
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 柱状图右翻
			_nextTap() {
				this.form.time = this.timeSelect.next
				console.log(this.timeSelect.next)
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 柱状图单位选择
			_unitTap(index) {
				this.form.timeUnit = index
				this.form.time = ''
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 一级tab点击
			change(index) {
				this.current = index
				this.page.pageNum = 1
				this.tdList = []
				this.form.time = ''
				this.form.tabCodeSub = 0
				this.getList()
			},
			// 二级tab点击
			_twoChange(index){
				this.page.pageNum = 1
				this.tdList = []
				this.form.tabCodeSub = index
				this.getList()
			}
		},
		components: {
			tabBar, tabs1, tabs2, tabs3, watermark
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}

	.container {
		.fixedTop {
			z-index: 4;
		}
	}
</style>
